import React from "react";
import { connect } from "react-redux";
import actionContacts from "../../redux/contacts/contacts-action";
import Button from "../Button";
import { ReactComponent as IconDelite } from "../../icon/delite.svg";
import styles from "./ContactsList.module.css";

const ContactList = ({ contacts, onDelete }) => (
  <ul className={styles.list}>
    {contacts.map((ele) => {
      return (
        <li key={ele.id} className={styles.item}>
          <p>{ele.name}</p>
          <p>{ele.number}</p>
          <Button onClick={() => onDelete(ele.id)} type="button">
            <IconDelite
              width="30"
              height="25"
              fill="white"
              aria-label="delite"
            />
          </Button>
        </li>
      );
    })}
  </ul>
);

const getFilterContacts = (contacts, filter) => {
  return contacts.filter((contact) =>
    contact.name.toLowerCase().includes(filter.toLowerCase())
  );
};

const mapStateToProps = ({ contacts: { items, filter } }) => ({
  contacts: getFilterContacts(items, filter),
});

const mapDispatchToProps = (dispatch) => ({
  onDelete: (id) => dispatch(actionContacts.DeleteContacts(id)),
});

export default connect(mapStateToProps, mapDispatchToProps)(ContactList);
